<template>
  <div>
    <el-container>
      <el-aside width="auto">
        <div class="title">
          <div class="title-box">
            <p v-if="isCollapse == true">JXWAF</p>
            <p v-if="isCollapse == false"><img :src="imgLogo" />JXWAF</p>
          </div>

        </div>
        <el-menu :router="true" background-color="#545c64" text-color="#fff" active-text-color="#409eff"
          :collapse="isCollapse" :default-active="activeIndex" class="el-menu-vertical">
          <el-submenu index="1">
            <template #title>
              <i class="el-icon-document"></i>
              <span>防护管理</span>
            </template>
            <el-menu-item index="/domain"><span>网站防护</span></el-menu-item>
            <el-menu-item index="/name-list"><span>名单防护</span></el-menu-item>
            <el-menu-item index="/base-component"><span>基础组件</span></el-menu-item>
            <el-menu-item index="/analysis-component"><span>分析组件</span></el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template #title>
              <i class="el-icon-s-data"></i>
              <span>运营中心</span>
            </template>
            <el-menu-item index="/soc-statistics"><span>业务数据统计</span></el-menu-item>
            <el-menu-item index="/soc-web-report"><span>Web安全报表</span></el-menu-item>
            <el-menu-item index="/soc-flow-report"><span>流量安全报表</span></el-menu-item>
            <el-menu-item index="/soc-attack-event"><span>攻击事件</span></el-menu-item>
            <el-menu-item index="/soc-query-log"><span>日志查询</span></el-menu-item>
            <el-menu-item index="/node-monitor"><span>节点状态</span></el-menu-item>
          </el-submenu>
          <el-submenu index="3">
            <template #title>
              <i class="el-icon-setting"></i>
              <span>系统配置</span>
            </template>
            <el-menu-item index="/waf-auth"><span>基础信息</span></el-menu-item>
            <el-menu-item index="/ssl-manage"><span>SSL证书管理</span></el-menu-item>
            <el-menu-item index="/sys-log-conf"><span>日志传输配置</span></el-menu-item>
            <el-menu-item index="/sys-report-conf"><span>日志查询配置</span></el-menu-item>
            <el-menu-item index="/sys-custom-deny-page-conf"><span>拦截页面配置</span></el-menu-item>
            <el-menu-item index="/conf-backup-load"><span>配置备份&加载</span></el-menu-item>
          </el-submenu>

        </el-menu>
      </el-aside>
      <el-container>
        <el-header>
          <div class="left link" @click="onClickFolding">
            <span>
              <i class="el-icon-more"></i>
            </span>
          </div>
          <div class="right link">
            <a href="/logout" target="_self" class="link">退出</a>
          </div>
        </el-header>
        <el-main>
          <router-view keep-alive transition="fade" transition-mode="out-in"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>

  <el-backtop :right="50" :bottom="50" />
</template>

<script>

import imgLogo from "./assets/images/logo1800.png";
import { useRouter } from 'vue-router';
export default {
  //mixins: [mixin],
  data() {
    return {
      route: '',
      isCollapse: false,
      activeIndex: "/domain",
      imgLogo: imgLogo,
      loadingPage: false,
    };
  },

  mounted() {
    var t = this;
    const { currentRoute } = useRouter();
    setTimeout(function () { 
      var url = currentRoute.value.path;
      var path = url.split("/")[1];
      var params = url.split("/")[3];
      t.activeMenu(path, params);
    }, 100)

  },

  watch: {
    $route(to, from) {
      var path = to.path.split("/")[1]; // web-rule-protection
      var params = to.path.split("/")[3]; // single_rule
      this.activeMenu(path, params);
    }
  },

  methods: {
    activeMenu(path, params) {
      var str = path;
      switch (str) {
        case "domain":
          this.activeIndex = "/domain"
          break;
        case "base-component":
          this.activeIndex = "/base-component"
          break;
        case "analysis-component":
          this.activeIndex = "/analysis-component"
          break;
        case "name-list":
        case "name-list-item":
        case "name-list-edit":
          this.activeIndex = "/name-list"
          break;
        case "waf-auth":
          this.activeIndex = "/waf-auth"
          break;
        case "ssl-manage":
          this.activeIndex = "/ssl-manage"
          break;
        case "sys-log-conf":
          this.activeIndex = "/sys-log-conf"
          break;
        case "sys-report-conf":
          this.activeIndex = "/sys-report-conf"
          break;
        case "sys-custom-deny-page-conf":
          this.activeIndex = "/sys-custom-deny-page-conf"
          break;
        case "conf-backup-load":
          this.activeIndex = "/conf-backup-load"
          break;
        case "node-monitor":
          this.activeIndex = "/node-monitor"
          break;
        case "soc-query-log":
          this.activeIndex = "/soc-query-log"
          break;
        case "soc-web-report":
          this.activeIndex = "/soc-web-report"
          break;
        case "soc-flow-report":
          this.activeIndex = "/soc-flow-report"
          break;
        case "soc-attack-event":
        case "soc-attack-event-behave":
          this.activeIndex = "/soc-attack-event"
          break;
        case "soc-statistics":
          this.activeIndex = "/soc-statistics"
          break;
        default:
          this.activeIndex = "/domain"
      }
    },
    onClickFolding() {
      this.isCollapse = !this.isCollapse;
    },
  },
};
</script>

<style>
#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  text-align: center;
  background: #4b5259;
}

.el-menu {
  text-align: left;
  border-right: none;
}

body > .el-container {
  margin-bottom: 40px;
}

.title {
  height: 60px;
  line-height: 60px;
  color: #fff;
  display: flex;
  justify-content: center;
}

.el-container {
  min-height: 100vh;
}
.el-header {
  padding: 0;
  font-size: 12px;
  text-align: left;
  background-color: #545c64;
}
.left,
.right {
  padding: 0 20px;
  cursor: pointer;
}
.el-menu-vertical:not(.el-menu--collapse) {
  width: 200px;
}
.header-select {
  width: 95px;
  margin-right: 10px;
}
.header-select input {
  background-color: transparent;
  border: none;
  color: #66b1ff;
}
.header-select i {
  color: #66b1ff !important;
}
.title-box p{
  display: flex;
}
.title-box img {
    width: 30px;
    height: 30px;
    top: 15px;
    position: relative;
    display: block;
    margin-left: -20px;
    margin-right: 8px;
}
.init-check-content .item {
   padding: 10px 20px;
}
.init-check-status {
  margin-left: 10px;
}
</style>
