import { createRouter, createWebHashHistory } from "vue-router";

const domain = () => import("./components/domain.vue")
const protection = () => import("./components/protection.vue")
const webEngineProtection = () => import("./components/web-engine-protection.vue")
const flowEngineProtection = () => import("./components/flow-engine-protection.vue")
const webRuleProtection = () => import("./components/web-rule-protection.vue")
const webRuleProtectionEdit = () => import("./components/web-rule-protection-edit.vue")
const webWhiteRule = () => import("./components/web-white-rule.vue")
const webWhiteRuleEdit = () => import("./components/web-white-rule-edit.vue")
const flowRuleProtection = () => import("./components/flow-rule-protection.vue")
const flowRuleProtectionEdit = () => import("./components/flow-rule-protection-edit.vue")
const flowIpRegionBlock = () => import("./components/flow-ip-region-block.vue")
const flowWhiteRule = () => import("./components/flow-white-rule.vue")
const flowWhiteRuleEdit = () => import("./components/flow-white-rule-edit.vue")
const nameList = () => import("./components/name-list.vue")
const nameListEdit = () => import("./components/name-list-edit.vue")
const nameListItem = () => import("./components/name-list-item.vue")
const baseComponent = () => import("./components/base-component.vue")
const analysisComponent = () => import("./components/analysis-component.vue")
const wafAuth = () => import("./components/waf-auth.vue")
const sslManage = () => import("./components/ssl-manage.vue")
const sysLogConf = () => import("./components/sys-log-conf.vue")
const sysReportConf = () => import("./components/sys-report-conf.vue")
const sysCustomDenyPageConf = () => import("./components/sys-custom-deny-page-conf.vue")
const confBackupLoad = () => import("./components/conf-backup-load.vue")
const nodeMonitor = () => import("./components/node-monitor.vue")
const QuerySearch = () => import("./components/soc-query-log.vue")
const WebReport = () => import("./components/soc-web-report.vue")
const flowReport = () => import("./components/soc-flow-report.vue")
const socAttackEvent = () => import("./components/soc-attack-event.vue")
const socAttackEventBehave = () => import("./components/soc-attack-event-behave.vue")
const socStatistics = () => import("./components/soc-statistics.vue")

let routes = [
  { path: "/", component: domain },
  { path: "/domain", component: domain },
  { path: "/protection/:domain", component: protection },
  { path: "/web-engine-protection/:domain", component: webEngineProtection },
  { path: "/flow-engine-protection/:domain", component: flowEngineProtection },
  { path: "/web-rule-protection/:domain", component: webRuleProtection },
  { path: "/web-rule-protection-edit/:domain/:uuid", component: webRuleProtectionEdit },
  { path: "/web-white-rule/:domain", component: webWhiteRule },
  { path: "/web-white-rule-edit/:domain/:uuid", component: webWhiteRuleEdit },
  { path: "/flow-rule-protection/:domain", component: flowRuleProtection },
  { path: "/flow-rule-protection-edit/:domain/:uuid", component: flowRuleProtectionEdit },
  { path: "/flow-ip-region-block/:domain", component: flowIpRegionBlock },
  { path: "/flow-white-rule/:domain", component: flowWhiteRule },
  { path: "/flow-white-rule-edit/:domain/:uuid", component: flowWhiteRuleEdit },
  { path: "/name-list", component: nameList },
  { path: "/name-list-edit/:uuid", component: nameListEdit },
  { path: "/name-list-item/:uuid", component: nameListItem },
  { path: "/base-component", component: baseComponent },
  { path: "/analysis-component", component: analysisComponent },
  { path: "/waf-auth", component: wafAuth },
  { path: "/ssl-manage", component: sslManage },
  { path: "/sys-log-conf", component: sysLogConf },
  { path: "/sys-report-conf", component: sysReportConf },
  { path: "/sys-custom-deny-page-conf", component: sysCustomDenyPageConf },
  { path: "/conf-backup-load", component: confBackupLoad },
  { path: "/node-monitor", component: nodeMonitor },
  { path: "/soc-query-log", component: QuerySearch },
  { path: "/soc-web-report", component: WebReport},
  { path: "/soc-flow-report", component: flowReport},
  { path: "/soc-attack-event", component: socAttackEvent },
  { path: "/soc-query-log/:uuid/:time", component: QuerySearch },
  { path: "/soc-query-log/:uuid/:host/:uri/:time", component: QuerySearch },
  { path: "/soc-attack-event-behave/:uuid", component: socAttackEventBehave },
  { path: "/soc-statistics", component: socStatistics },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes: routes
})

export default router